              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="panel-item">
              <div class="panel-label"></div>
              <div class="panel-content" :title="cardServiceInfo.openerPhone">
                {{ cardServiceInfo.openerPhone }}
              </div>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="panel-item">
