              >
                {{ cardServiceInfo.businessManName }}
              </div>
            </div>
          </el-col>
        </el-row>
        <el-row :gutter="16">
          <el-col :span="12">
            <div class="panel-item">
              <div class="panel-label"></div>
              <div
                class="panel-content color-warning"
