<template>
  <div class="vip-card-type" v-loading="loading">
    <VipCardSearch
      :formData="formData"
      :moreVisible="false"
      placeholderText=""
      labelWidth="96px"
      @handleQuery="handleQuery"
      @handleReset="handleReset"
    >
      <el-button slot="right" size="small" type="primary" @click="addVipCard">
        
      </el-button>
    </VipCardSearch>
    <el-table
      class="table-base"
      size="small"
      :data="tableData"
      stripe
      border
      style="width: 100%"
      height="400px"
    >
      <el-table-column
        label=""
        prop="name"
        width="100"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label=""
        prop="color"
        width="86"
        show-overflow-tooltip
      >
        <template slot-scope="scope">
          <div
            class="card-grade-bg"
            :style="{ background: scope.row.color }"
          ></div>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="descreption"
        width="150"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="" prop="useLimit" width="200">
        <template slot-scope="scope">
          <div>
            <div v-if="scope.row.useLimit.branch.limit">
              <span></span>
              <span>
                {{
                  scope.row.useLimit.branch.rows
                    ? scope.row.useLimit.branch.rows
                        .map(item => item.name)
                        .join("")
                    : ""
                }}
              </span>
            </div>
            <template v-else>
              {{ "" }}
            </template>
          </div>
          <div v-if="$store.state.base.companyType === 'garage'">
            {{ scope.row.useLimit.vehicle.limit ? "" : "" }}
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="80" show-overflow-tooltip>
        <template slot-scope="scope">
          {{ scope.row.free ? "" : scope.row.price }}
        </template>
      </el-table-column>
      <el-table-column label="" min-width="80" show-overflow-tooltip>
        <template slot-scope="scope">
          <div :class="{ 'color-error': Number(scope.row.qty) < 10 }">
            {{ scope.row.qty }}
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" width="80" show-overflow-tooltip>
        <div class="card-state" slot-scope="scope">
          <i
            class="state-dot"
            :class="[scope.row.status === 1 ? 'state-enable' : 'state-disable']"
          ></i>
          {{ scope.row.status === 1 ? "" : "" }}
        </div>
      </el-table-column>
      <el-table-column
        label=""
        prop="openerQty"
        width="102"
        show-overflow-tooltip
        sortable
      >
        <template slot-scope="scope">
          <div
            v-if="Number(scope.row.openerQty)"
            @click="goCardManage(scope.row)"
            class="opener-qty"
          >
            {{ scope.row.openerQty }}
          </div>
          <div v-else>
            {{ scope.row.openerQty }}
          </div>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="createdAt"
        width="106"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label=""
        prop="createdBy"
        min-width="100"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button
            type="text"
            size="mini"
            @click="handleEdit(scope.row)"
            style="margin-right: 16px;"
          >
            
          </el-button>

          <el-popover
            placement="bottom"
            trigger="click"
            popper-class="popover-select"
          >
            <div
              class="popover-option"
              @click="updateMemberCardStatus(scope.row)"
            >
              {{ scope.row.status === 1 ? "" : "" }}
            </div>
            <div class="popover-option" @click="handleCopy(scope.row)">
              
            </div>
            <el-button slot="reference" type="text" size="mini"></el-button>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="pagination-base"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      background
      :current-page="pageData.page"
      :page-sizes="pageData.pageSizes"
      :page-size="pageData.pageSize"
      :pager-count="pageData.pagerCount"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageData.total"
    >
    </el-pagination>
  </div>
</template>

<script>
import VipCardSearch from "../components/vipCardSearch.vue";
import { getMemberCards, updateMemberCardStatus } from "../services";
import baseIndexJump from "@/utils/baseIndexJump";

export default {
  name: "VipCardType",
  components: {
    VipCardSearch
  },
  data() {
    return {
      loading: false,
      pageData: {
        page: 1,
        pageSizes: [10, 20, 50, 100],
        pageSize: 10,
        total: 0,
        pagerCount: 5
      },
      formData: {
        keyword: ""
      },
      cardStatusOptions: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "1"
        },
        {
          value: "2",
          label: "2"
        }
      ],
      operatorOptions: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "1"
        },
        {
          value: "2",
          label: "2"
        }
      ],
      cardTypeOptions: [
        {
          value: "0",
          label: ""
        },
        {
          value: "1",
          label: "1"
        },
        {
          value: "2",
          label: "2"
        }
      ],
      tableData: [
        // {
        //   id: 1,
        //   name: "", // 
        //   color: "", // 
        //   descreption: "", // 
        //   // 
        //   useLimit: {
        //     // 
        //     branch: {
        //       limit: true, // 
        //       // 
        //       rows: [
        //         {
        //           id: 1, // ID
        //           name: "1" // 
        //         },
        //         {
        //           id: 2,
        //           name: "2"
        //         },
        //         {
        //           id: 3,
        //           name: "3"
        //         },
        //         {
        //           id: 4,
        //           name: "4"
        //         },
        //         {
        //           id: 5,
        //           name: "5"
        //         },
        //         {
        //           id: 6,
        //           name: "6"
        //         },
        //         {
        //           id: 7,
        //           name: "7"
        //         }
        //       ]
        //     },
        //     // 
        //     vehicle: {
        //       limit: true // truefalse
        //     }
        //   },
        //   free: true, // 
        //   price: "", // null
        //   qty: "10", // 
        //   status: 1, // ,1:;2:
        //   openerQty: "10", // 
        //   createdAt: "2020-12-30", // 
        //   createdBy: "" // 
        // }
      ]
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageData.page = 1;
      this.pageData.pageSize = val;
      this.getDatas();
    },
    handleCurrentChange(val) {
      this.pageData.page = val;
      this.getDatas();
    },
    handleQuery() {
      this.pageData.page = 1;
      this.getDatas();
    },
    handleReset() {
      this.formData.keyword = "";
    },
    // 
    addVipCard() {
      this.$router.push({ path: "/vipCard/add" });
    },
    // 
    handleCopy(row) {
      this.$router.push({ path: "/vipCard/add", query: { id: row.id } });
    },
    // 
    handleEdit(row) {
      this.$router.push({ path: "/vipCard/edit", query: { id: row.id } });
    },
    // 
    updateMemberCardStatus(row) {
      if (!row.id) return;
      const enable = row.status === 1 ? false : true;
      let title = "";
      let message = "";
      if (enable) {
        title = "";
        message = "";
      }
      this.$confirm(message, title, {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        showClose: false
      })
        .then(() => {
          this.loading = true;
          const params = {
            enable
          };
          updateMemberCardStatus(params, row.id)
            .then(() => {
              this.loading = false;
              this.$message.success(enable ? "" : "");
              this.handleQuery();
            })
            .catch(() => {
              this.loading = false;
            });
        })
        .catch(() => {});
    },
    // 
    getDatas() {
      this.loading = true;
      const params = {
        ...this.formData,
        page: this.pageData.page,
        pageSize: this.pageData.pageSize
      };
      getMemberCards(params)
        .then(res => {
          this.pageData.total = res.totalSize || 0;
          this.tableData = res.rows || [];
          this.loading = false;
        })
        .catch(() => {
          this.pageData.total = 0;
          this.tableData = [];
          this.loading = false;
        });
    },
    goCardManage(row) {
      baseIndexJump.setInfo("vipCardManage", row.id);
      const { fullPath } = this.$route;
      this.$router.replace({
        path: "/redirect" + fullPath,
        query: {
          tabName: "VipCardManage"
        }
      });
    }
  },
  created() {
    this.getDatas();
  }
};
</script>
<style lang="less" scoped>
.vip-card-type {
  height: 100%;
  display: flex;
  flex-direction: column;
  .table-base {
    flex: 1;
    margin-top: @margin-size-main;
    .card-grade-bg {
      width: 64px;
      height: 24px;
    }
    .card-state {
      display: flex;
      align-items: center;
      .state-dot {
        display: inline-block;
        width: 8px;
        height: 8px;
        border-radius: 50% 50%;
        margin-right: @margin-size-secondary;
      }
      .state-enable {
        background-color: #389e0d;
      }
      .state-disable {
        background-color: #595959;
      }
    }
    .opener-qty {
      color: @color-link;
      cursor: pointer;
    }
  }
  .pagination-base {
    text-align: right;
    padding-top: 12px;
    // border-top: 1px solid @border-color-base;
  }
  /deep/ .el-icon-question {
    font-size: 16px;
    color: @color-warning;
    margin-left: @margin-size-secondary;
    cursor: pointer;
  }
}
</style>
