<script>
import VipCardSearch from "../components/vipCardSearch.vue";
import {
  getMemberCardLogs,
  getStaffs,
  getCompanies,
  exportMembersCardServiceLogs
} from "../services";
import baseIndexJump from "@/utils/baseIndexJump";
import { downloadBlob } from "@/utils/tool";

const defaultFormData = {
  keyword: "", // 
  opener: "", // 
  memberCardNo: "", // 
  operateType: [], // 
  createdDate: null, // 
  // createdStart: "", // 
  // createdEnd: "", // 
  createdCompanyId: "", // 
  createdBy: "", // id
  businessManName: "" // 
};
export default {
  name: "VipCardLog",
  components: {
    VipCardSearch
  },
  watch: {
    $route: "routeChange"
  },
  data() {
    return {
      loading: false,
      selectLoading: false,
      pageData: {
        page: 1,
        pageSizes: [10, 20, 50, 100],
        pageSize: 10,
        total: 0,
        pagerCount: 5
      },
      formData: JSON.parse(JSON.stringify(defaultFormData)),
      board: {
        openerCount: "", // 
        cardCount: "", // 
        businessAmount: "", // 
        giftAmount: "" // 
      },
      tableData: [
        {
          id: 1, // ID
          customerName: "", // 
          opener: "", // 
          memberCardName: "", // 
          memberCardNo: "", //
          operateTypeName: "", // 
          amount: "100", // null
          content: [""], // 
          createdCompany: "", // 
          channel: "", // 
          createdBy: "", // 
          createdAt: "", // YYYY-MM-DD
          remarks: "" // 
        }
      ],
      // 0:;1:;2.;3.;4.;5.;6.;7.;,
      operateTypeOptions: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        },
        {
          value: "4",
          label: ""
        },
        {
          value: "5",
          label: ""
        },
        {
          value: "6",
          label: ""
        },
        {
          value: "7",
          label: ""
        }
      ],
      companieOptions: [],
      createdByOptions: [],
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    getCompanies() {
      getCompanies()
        .then(res => {
          this.companieOptions = res || [];
        })
        .catch(() => {
          this.companieOptions = [];
        });
    },
    routeChange(to) {
      if (to.path === "/vipCard" && to.query.tabName === "VipCardLog") {
        const { jumpId, jumpData } = baseIndexJump.getInfo();
        if (jumpId === "vipCardLog" && jumpData) {
          this.formData.keyword = jumpData;
          baseIndexJump.clearInfo();
          this.getDatas();
        }
      }
    },
    handleQuery() {
      this.pageData.page = 1;
      this.getDatas();
    },
    handleReset() {
      this.formData = JSON.parse(JSON.stringify(defaultFormData));
    },
    handleSizeChange(val) {
      this.pageData.page = 1;
      this.pageData.pageSize = val;
      this.getDatas();
    },
    handleCurrentChange(val) {
      this.pageData.page = val;
      this.getDatas();
    },
    queryStaffs(query) {
      const params = {
        name: query,
        page: 1,
        pageSize: 50
      };
      getStaffs(params)
        .then(res => {
          this.createdByOptions = res.rows || [];
          this.selectLoading = false;
        })
        .catch(() => {
          this.createdByOptions = [];
          this.selectLoading = false;
        });
    },
    // 
    getDatas(isExport = false) {
      const params = JSON.parse(JSON.stringify(this.formData));
      params.page = this.pageData.page;
      params.pageSize = this.pageData.pageSize;
      params.operateType = params.operateType.join(",");
      if (params.createdDate) {
        params.createdStart = params.createdDate[0];
        params.createdEnd = params.createdDate[1];
      }
      delete params.createdDate;
      if (isExport) {
        this.exportMembersCardServiceLogs(params);
      } else {
        this.getMemberCardLogs(params);
      }
    },
    exportMembersCardServiceLogs(params) {
      this.loading = true;
      exportMembersCardServiceLogs(params)
        .then(res => {
          downloadBlob(res, ".xlsx");
          this.loading = false;
          this.$message.success("");
        })
        .catch(() => (this.loading = false));
    },
    getMemberCardLogs(params) {
      this.loading = true;
      getMemberCardLogs(params)
        .then(res => {
          this.pageData.total = res.totalSize || 0;
          this.tableData = res.rows || [];
          this.board = {
            openerCount: res.openerCount || "-",
            cardCount: res.cardCount || "-",
            businessAmount: res.businessAmount || "-",
            giftAmount: res.giftAmount || "-"
          };
          this.loading = false;
        })
        .catch(() => {
          this.pageData.total = 0;
          this.tableData = [];
          this.board = {
            openerCount: "-",
            cardCount: "-",
            businessAmount: "-",
            giftAmount: "-"
          };
          this.loading = false;
        });
    }
  },
  created() {
    const { jumpId, jumpData } = baseIndexJump.getInfo();
    if (jumpId === "vipCardLog" && jumpData) {
      this.formData.keyword = jumpData;
      baseIndexJump.clearInfo();
    }
    this.getDatas();
    this.queryStaffs("");
    this.getCompanies();
  }
};
</script>

