        <el-form-item label="" prop="businessManName">
          <el-input
            v-model.trim="formData.businessManName"
            maxlength="10"
            size="small"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="formData.createdDate"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="formData.discountExpiredDate"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="formData.packageExpiredDate"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="formData.latelyUseDate"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions"
            value-format="yyyy-MM-dd"
          >
          </el-date-picker>
        </el-form-item>
      </template>
    </VipCardSearch>
    <div class="action-bar">
      <div class="flex-1">
        <el-button
          size="small"
          type="primary"
          plain
          @click="showCardServiceAddDialog"
        >
          
        </el-button>
        <el-button
          size="small"
          type="primary"
          plain
          @click="showCardServiceRechargeDialog()"
          :disabled="!tableData.length"
        >
          
        </el-button>
        <el-button
          size="small"
          type="primary"
          plain
          @click="showCardServiceUpdateDialog()"
          :disabled="!tableData.length"
        >
          
        </el-button>
        <!-- <el-button size="small" type="primary"></el-button> -->
        <el-button
          size="small"
          type="primary"
          :disabled="!tableData.length"
          @click="getDatas(true)"
        >
          
        </el-button>
      </div>
      <template>
        
        <span> {{ board.openerCount }}</span>
        <el-divider direction="vertical" class="divider-base"></el-divider>
        <span> {{ board.cardCount }}</span>
        <el-divider direction="vertical" class="divider-base"></el-divider>
        <span> {{ board.rechargeTotalAmount }}</span>
        <el-divider direction="vertical" class="divider-base"></el-divider>
        <span> {{ board.cashBalanceTotalAmount }}</span>
        <el-divider direction="vertical" class="divider-base"></el-divider>
        <span> {{ board.openTotalAmount }}</span>
      </template>
    </div>
    <el-table
      class="table-base"
      size="small"
      :data="tableData"
      stripe
      border
      style="width: 100%"
      height="400px"
    >
      <el-table-column label="" width="150" show-overflow-tooltip>
        <template slot-scope="scope">
          {{
            scope.row.customerName
              ? scope.row.customerName +
                "-" +
                scope.row.customerContacts +
                "(" +
                scope.row.customerPhone +
                ")"
              : scope.row.customerContacts + "(" + scope.row.customerPhone + ")"
          }}
          <div class="color-error" v-if="scope.row.customerStatus === 2">
            
          </div>
          <div class="color-error" v-if="scope.row.customerStatus === 3">
            
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" width="150" show-overflow-tooltip>
        <template slot-scope="scope">
          {{ scope.row.openerName }}
          <template v-if="scope.row.openerPhone">
            {{ "(" + scope.row.openerPhone + ")" }}
          </template>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="cardNo"
        width="190"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label=""
        prop="name"
        width="100"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label=""
        width="70"
        prop="statusName"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="" show-overflow-tooltip width="200">
        <template slot-scope="scope">
          <div v-if="scope.row.useLimit.branch.limit">
            <span class="color-secondary"></span>
            {{
              scope.row.useLimit.branch.rows &&
                scope.row.useLimit.branch.rows.map(item => item.name).join("")
            }}
          </div>
          <div class="color-hint" v-if="!scope.row.useLimit.branch.limit">
            
          </div>
          <template v-if="isGarage">
            <div v-if="scope.row.useLimit.vehicle.limit">
              <span class="color-secondary"></span>
              {{
                scope.row.useLimit.vehicle.rows &&
                  scope.row.useLimit.vehicle.rows
                    .map(item => item.name)
                    .join("")
              }}
            </div>
            <div class="color-hint" v-if="!scope.row.useLimit.vehicle.limit">
              
            </div>
          </template>
        </template>
      </el-table-column>
      <el-table-column label="" show-overflow-tooltip width="158">
        <template slot-scope="scope" v-if="scope.row.status !== 3">
          <div v-if="isGarage && scope.row.discount.detail.project">
            <span class="color-secondary"></span>
            {{ scope.row.discount.detail.project }}
          </div>
          <div v-if="scope.row.discount.detail.part">
            <span class="color-secondary"></span>
            {{ scope.row.discount.detail.part }}
          </div>
          <div v-if="isGarage && scope.row.discount.detail.package">
            <span class="color-secondary"></span>
            {{ scope.row.discount.detail.package }}
          </div>
          <div class="color-hint" v-if="!scope.row.discount.indate.limit">
            
          </div>
          <template v-if="scope.row.discount.indate.limit">
            <div class="color-hint" v-if="scope.row.discount.indate.date">
              {{ scope.row.discount.indate.date }}
            </div>
            <div class="color-error" v-if="scope.row.discount.indate.str">
              {{ scope.row.discount.indate.str }}
            </div>
          </template>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="rechargeTotalAmount"
        show-overflow-tooltip
        width="140"
      >
        <template slot="header">
          
          <el-tooltip
            effect="dark"
            content=""
            placement="bottom"
          >
            <i class="el-icon-question"></i>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="" width="300">
        <div
          slot-scope="scope"
          style="cursor: pointer;"
          @click="showCardServiceInfoDialog(scope.row)"
        >
          <div v-if="scope.row.cardBalance.cash">
            <span class="color-secondary"></span>
            {{ scope.row.cardBalance.cash }}
          </div>
          <template v-if="isGarage">
            <div class="panel" v-if="scope.row.projectsAndPackages">
              <div class="color-secondary"></div>
              <div class="panel-content" :title="scope.row.projectsAndPackages">
                {{ scope.row.projectsAndPackages }}
              </div>
            </div>
            <div
              class="color-error"
              v-if="
                scope.row.cardBalance.indate.limit &&
                  scope.row.cardBalance.indate.str
              "
            >
              {{ scope.row.cardBalance.indate.str }}
            </div>
          </template>
          <el-button type="text" size="mini"></el-button>
        </div>
      </el-table-column>
      <el-table-column label="" width="80" show-overflow-tooltip>
        <template slot-scope="scope">
          {{ scope.row.price ? scope.row.price : "-" }}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="createdCompany"
        width="100"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label=""
        prop="createdAt"
        show-overflow-tooltip
        width="160"
      ></el-table-column>
      <el-table-column label="" show-overflow-tooltip width="80">
        <template slot-scope="scope">
          {{ scope.row.businessManName ? scope.row.businessManName : "-" }}
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="createdBy"
        width="80"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        label=""
        prop="remarks"
        show-overflow-tooltip
        width="150"
      ></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.status === 3"
            type="text"
            size="mini"
            @click="handleLog(scope.row)"
          >
            
          </el-button>
          <template v-if="scope.row.status !== 3">
            <el-button
              type="text"
              size="mini"
              style="margin-right: 16px"
              @click="showCardServiceRechargeDialog(scope.row)"
            >
              
            </el-button>
            <el-popover
              placement="bottom"
              trigger="click"
              popper-class="popover-select"
            >
              <div
                class="popover-option"
                @click="showCardServiceUpdateDialog(scope.row)"
              >
                
              </div>
              <div
                class="popover-option"
                v-if="scope.row.status === 1"
                @click="updateStatus(scope.row, 2)"
              >
                
              </div>
              <div
                class="popover-option"
                v-if="scope.row.status === 2"
                @click="updateStatus(scope.row, 1)"
              >
                
              </div>
              <div class="popover-option" @click="returnCard(scope.row)">
                
              </div>
              <div class="popover-option" @click="handleLog(scope.row)">
                
              </div>
              <el-button slot="reference" type="text" size="mini">
                
              </el-button>
            </el-popover>
          </template>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="pagination-base"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      background
      :current-page="pageData.page"
      :page-sizes="pageData.pageSizes"
      :page-size="pageData.pageSize"
      :pager-count="pageData.pagerCount"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageData.total"
    >
    </el-pagination>
    <!--  -->
    <CardServiceAddDialog
      :isGarage="isGarage"
      @hide="hideCardServiceAddDialog"
      @update="getDatas"
      v-if="cardServiceAddVisible"
    ></CardServiceAddDialog>
    <!--  -->
    <CardServiceRechargeDialog
      :isGarage="isGarage"
      :now="rechargeNow"
      @hide="hideCardServiceRechargeDialog"
      @update="getDatas"
      v-if="cardServiceRechargeVisible"
    ></CardServiceRechargeDialog>
    <!--  -->
    <CardServiceUpdateDialog
      :isGarage="isGarage"
      :now="updateNow"
      @hide="hideCardServiceUpdateDialog"
      @update="getDatas"
      v-if="cardServiceUpdateVisible"
    ></CardServiceUpdateDialog>
    <!--  -->
    <CardServiceInfoDialog
      :isGarage="isGarage"
      :now="infoNow"
      @hide="hideCardServiceInfoDialog"
      v-if="cardServiceInfoVisible"
    ></CardServiceInfoDialog>
    <!--  -->
    <ReturnCardDialog
      ref="returnCardDialog"
      :isGarage="isGarage"
      @update="handleQuery"
      @showInfo="showCardServiceInfoDialog"
    ></ReturnCardDialog>
  </div>
</template>

<script>
import VipCardSearch from "../components/vipCardSearch.vue";
import {
  getMemberCardServices,
  uptateMemberCardServiceStatus,
  getMemberCardOptions,
  getStaffs,
  exportMembersCardServices
} from "../services";
import CardServiceAddDialog from "./components/cardServiceAddDialog.vue";
import CardServiceRechargeDialog from "./components/cardServiceRechargeDialog.vue";
import CardServiceUpdateDialog from "./components/cardServiceUpdateDialog.vue";
import CardServiceInfoDialog from "./components/cardServiceInfoDialog.vue";
import ReturnCardDialog from "./components/returnCardDialog.vue";
import baseIndexJump from "@/utils/baseIndexJump";
import { downloadBlob } from "@/utils/tool";

const defaultFormData = {
  keyword: "",
  cardNo: "", // 
  cardId: [], // 
  status: [], //  ,1:;2:;3:
  plateNumber: "", // 
  createdDate: null, // 
  // createdStart: "", // 
  // createdEnd: "", // 
  discountExpiredDate: null, // 
  // discountExpiredStart: "", // 
  // discountExpiredEnd: "", // 
  packageExpiredDate: null, // 
  // packageExpiredStart: "", // 
  // packageExpiredEnd: "", // 
  latelyUseDate: null, // 
  // latelyUseStart: "", // 
  // latelyUseEnd: "", // 
  createdBy: [], // 
  businessManName: "" // 
};

export default {
  name: "VipCardManage",
  components: {
    VipCardSearch,
    ReturnCardDialog,
    CardServiceAddDialog,
    CardServiceRechargeDialog,
    CardServiceUpdateDialog,
    CardServiceInfoDialog
  },
  computed: {
    isGarage() {
      return this.$store.state.base.companyType === "garage";
    }
  },
  watch: {
    $route: "routeChange"
  },
  data() {
    return {
      loading: false,
      selectLoading: false,
      cardServiceAddVisible: false,
      cardServiceRechargeVisible: false,
      cardServiceUpdateVisible: false,
      cardServiceInfoVisible: false,
      pageData: {
        page: 1,
        pageSizes: [10, 20, 50, 100],
        pageSize: 10,
        total: 0,
        pagerCount: 5
      },
      formData: JSON.parse(JSON.stringify(defaultFormData)),
      statusOptions: [
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      createdByOptions: [],
      memberCardOptions: [],
      board: {
        openerCount: "", // 
        cardCount: "", // 
        rechargeTotalAmount: "", // 
        cashBalanceTotalAmount: "", // 
        openTotalAmount: "" // 
      },
      tableData: [],
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      rechargeNow: null,
      updateNow: null,
      infoNow: null
    };
  },
  methods: {
    routeChange(to) {
      if (to.path === "/vipCard" && to.query.tabName === "VipCardManage") {
        const { jumpId, jumpData } = baseIndexJump.getInfo();
        if (jumpId === "vipCardManage" && jumpData) {
          this.formData.cardId.push(Number(jumpData));
          baseIndexJump.clearInfo();
          this.getDatas();
        }
      }
    },
    handleQuery() {
      this.pageData.page = 1;
      this.getDatas();
    },
    handleReset() {
      this.formData = JSON.parse(JSON.stringify(defaultFormData));
    },
    handleSizeChange(val) {
      this.pageData.page = 1;
      this.pageData.pageSize = val;
      this.getDatas();
    },
    handleCurrentChange(val) {
      this.pageData.page = val;
      this.getDatas();
    },
    queryStaffs(query) {
      const params = {
        name: query,
        page: 1,
        pageSize: 50
      };
      getStaffs(params)
        .then(res => {
          this.createdByOptions = res.rows || [];
          this.selectLoading = false;
        })
        .catch(() => {
          this.createdByOptions = [];
          this.selectLoading = false;
        });
    },
    // 
    getDatas(isExport = false) {
      const formData = this.formData;
      const params = {
        ...formData,
        cardId: formData.cardId.join(","),
        status: formData.status.join(","),
        createdBy: formData.createdBy.join(","),
        page: this.pageData.page,
        pageSize: this.pageData.pageSize
      };
      if (params.createdDate) {
        params.createdStart = params.createdDate[0];
        params.createdEnd = params.createdDate[1];
      }
      delete params.createdDate;
      if (params.discountExpiredDate) {
        params.discountExpiredStart = params.discountExpiredDate[0];
        params.discountExpiredEnd = params.discountExpiredDate[1];
      }
      delete params.discountExpiredDate;
      if (params.packageExpiredDate) {
        params.packageExpiredStart = params.packageExpiredDate[0];
        params.packageExpiredEnd = params.packageExpiredDate[1];
      }
      delete params.latelyUseDate;
      if (params.latelyUseDate) {
        params.latelyUseStart = params.latelyUseDate[0];
        params.latelyUseEnd = params.latelyUseDate[1];
      }
      delete params.latelyUseDate;
      if (isExport === true) {
        this.exportMembersCardServices(params);
      } else {
        this.getMemberCardServices(params);
      }
    },
    exportMembersCardServices(params) {
      this.loading = true;
      exportMembersCardServices(params)
        .then(res => {
          downloadBlob(res, ".xlsx");
          this.loading = false;
          this.$message.success("");
        })
        .catch(() => (this.loading = false));
    },
    pickList(list) {
      this.tableData = list.map(item => {
        let arr = [];
        if (item.cardBalance.projects) {
          arr = [...item.cardBalance.projects];
        }
        if (item.cardBalance.packages) {
          arr = [...arr, ...item.cardBalance.packages];
        }
        return {
          ...item,
          projectsAndPackages: arr
            .map(project => project.name + "*" + project.qty)
            .join("")
        };
      });
    },
    getMemberCardServices(params) {
      this.loading = true;
      getMemberCardServices(params)
        .then(res => {
          this.pageData.total = res.totalSize || 0;
          this.pickList(res.rows || []);
          this.board = {
            openerCount: res.openerCount || "-",
            cardCount: res.cardCount || "-",
            rechargeTotalAmount: res.rechargeTotalAmount || "-",
            cashBalanceTotalAmount: res.cashBalanceTotalAmount || "-",
            openTotalAmount: res.openTotalAmount || "-"
          };
          this.loading = false;
        })
        .catch(() => {
          this.pageData.total = 0;
          this.tableData = [];
          this.board = {
            openerCount: "-",
            cardCount: "-",
            rechargeTotalAmount: "-",
            cashBalanceTotalAmount: "-",
            openTotalAmount: "-"
          };
          this.loading = false;
        });
    },
    // id
    queryMemberCards() {
      getMemberCardOptions({
        all: true
      })
        .then(res => {
          this.memberCardOptions = res || [];
        })
        .catch(() => {
          this.memberCardOptions = [];
        });
    },
    // /  status 1: 2:
    updateStatus(row, status) {
      let title = "";
      let message = "";
      if (status === 1) {
        title = "";
        message = "";
      }
      this.$confirm(message, title, {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        showClose: false
      })
        .then(() => {
          this.loading = true;
          const params = { status };
          uptateMemberCardServiceStatus(params, row.id)
            .then(() => {
              this.$message.success(`${title}!`);
              this.handleQuery();
              this.loading = false;
            })
            .catch(() => {
              this.loading = false;
            });
        })
        .catch(() => {});
    },
    // 
    returnCard(row) {
      if (row.customerStatus === 2) {
        this.$message.error(
          "->->"
        );
      } else {
        this.$refs.returnCardDialog.show(row);
      }
    },
    // 
    showCardServiceAddDialog() {
      this.cardServiceAddVisible = true;
    },
    hideCardServiceAddDialog() {
      this.cardServiceAddVisible = false;
    },
    // 
    showCardServiceRechargeDialog(row) {
      if (row && row.customerStatus === 2) {
        this.$message.error(
          "->->"
        );
      } else {
        this.rechargeNow = row || null;
        this.cardServiceRechargeVisible = true;
      }
    },
    hideCardServiceRechargeDialog() {
      this.cardServiceRechargeVisible = false;
    },
    // 
    showCardServiceUpdateDialog(row) {
      if (row && row.customerStatus === 2) {
        this.$message.error(
          "->->"
        );
      } else {
        this.updateNow = row || null;
        this.cardServiceUpdateVisible = true;
      }
    },
    hideCardServiceUpdateDialog() {
      this.cardServiceUpdateVisible = false;
    },
    // 
    showCardServiceInfoDialog(row) {
      this.infoNow = row || null;
      this.cardServiceInfoVisible = true;
    },
    hideCardServiceInfoDialog() {
      this.cardServiceInfoVisible = false;
    },
    // 
    handleLog(row) {
      baseIndexJump.setInfo("vipCardLog", row.cardNo);
      const { fullPath } = this.$route;
      this.$router.replace({
        path: "/redirect" + fullPath,
        query: {
          tabName: "VipCardLog"
        }
      });
    }
  },
  created() {
    const { jumpId, jumpData } = baseIndexJump.getInfo();
    if (jumpId === "vipCardManage" && jumpData) {
      this.formData.cardId.push(Number(jumpData));
      baseIndexJump.clearInfo();
    }
    if (jumpId === "getCustomerId" && jumpData) {
      this.formData.customerId = Number(jumpData);
      baseIndexJump.clearInfo();
    }
    this.getDatas();
    this.queryMemberCards();
    this.queryStaffs("");
  }
};
</script>

