// 
export function integerValidate(value) {
  value = String(value);
  value = value.replace(/[^\d]/g, "");
  value = value.replace(/^0{2,}/g, "0");
  // if (Number(value) > 9999) {
  if (value.length > 10) {
    // value = "9999";
    value = value.substring(0, 10);
  }
  return String(value);
}
// 
export function positiveIntegerValidate(value) {
  value = String(value);
  value = value.replace(/[^\d]/g, "");
  value = value.replace(/^0/g, "");
  if (value.length > 10) {
    value = value.substring(0, 10);
  }
  return String(value);
}

// 
export function floatNumberValidate(value) {
  value = String(value);
  // .
  value = value.replace(/[^\d.]/g, "");
  // .
  value = value.replace(/^\./g, "");
  // 00
  value = value.replace(/^0{2,}/g, "0");
  // .
  value = value
    .replace(".", "$#$")
    .replace(/\./g, "")
    .replace("$#$", ".");
  let index = -1;
  for (const i in value) {
    if (value[i] === ".") {
      index = i;
    }
    if (index !== -1) {
      if (i - index > 2) {
        value = value.substring(0, value.length - 1);
      }
    }
  }
  // 10
  if (Number(value) >= 10000000000) {
    value = "9999999999.99";
  }
  return value;
}
export function zeroValidate(value) {
  if(Number(value) > 0) {
    return value;
  } else {
    return "";
  }
}

// 
export function discountValidate(value) {
  value = String(value);
  // .
  value = value.replace(/[^\d.]/g, "");
  // .
  value = value.replace(/^\./g, "");
  // value = value.replace(/^0/g, "");
  // 00
  // value = value.replace(/^0{2,}/g, "0");
  value = value.replace(/^0+[^.]/g, "0");
  // .
  value = value
    .replace(".", "$#$")
    .replace(/\./g, "")
    .replace("$#$", ".");
  let index = -1;
  for (const i in value) {
    if (value[i] === ".") {
      index = i;
    }
    if (index !== -1) {
      if (i - index > 1) {
        value = value.substring(0, value.length - 1);
      }
    }
  }
  // 10
  if (Number(value) >= 10) {
    value = "9.9";
  }
  return value;
}