<template>
  <el-form
    class="form-search"
    size="small"
    :model="formData"
    :label-width="labelWidth"
  >
    <div class="search-layout">
      <div class="search-layout-left">
        <el-form-item label="" label-width="56px" style="width: 320px; margin-right: 24px;">
          <el-input
            v-model="formData.keyword"
            clearable
            :placeholder="placeholderText"
          ></el-input>
        </el-form-item>
        <slot name="query">
          <el-button size="small" type="primary" @click="handleQuery">
            
          </el-button>
        </slot>
        <slot name="reset">
          <el-button size="small" @click="handleReset"></el-button>
        </slot>
        <el-button
          size="small"
          @click="searchSwitch = !searchSwitch"
          v-if="moreVisible"
        >
          
          <i
            :class="searchSwitch ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          ></i>
        </el-button>
      </div>
      <slot name="right"></slot>
    </div>
    <div class="search-box" v-show="searchSwitch">
      <slot name="more"></slot>
    </div>
  </el-form>
</template>

<script>
export default {
  name: "VipCardSearch",
  props: {
    formData: Object,
    labelWidth: String,
    placeholderText: {
      type: String,
      default: "///"
    },
    moreVisible: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      searchSwitch: false
    };
  },
  methods: {
    handleQuery() {
      this.$emit("handleQuery");
    },
    handleReset() {
      this.$emit("handleReset");
    }
  }
};
</script>

